.personal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 32px;
  color: #000;
}
.personaldata-content {
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 8px;
  padding: 24px 32px;
}
.personaldata-content .ant-form-item {
  margin-bottom: 0;
}
.personaldata-content .ant-col {
  margin-bottom: 12px;
}
.personaldata-content .ant-input {
  height: 48px;
}
.personaldata-content .picker {
  width: 100%;
}
.personaldata-content .bb8 {
  margin-bottom: 8px;
}
.personaldata-content .login-form-button {
  float: right;
}
.ledit {
  color: #fff;
  background-color: #0091FF;
  border-color: #0091FF;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
  text-align: center;
  padding: 0 20px;
  float: right;
  border-radius: 4px;
  line-height: 2.5;
}
.ledit:hover {
  cursor: pointer;
}
.cancel {
  float: right;
  margin-right: 20px;
}
